import React, { Component } from 'react';
import './noi12.css'
var noi12_json;
var stt = [];
class noi12 extends Component {
    constructor() {
        super()
        this.state = {
            page: 'home',
            item: 0
        }
    }
    fullScreen(b) {
        let elem = document.documentElement;
        if (b) {
            if (elem.requestFullscreen) {
                elem.requestFullscreen();
            } else if (elem.mozRequestFullScreen) { /* Firefox */
                elem.mozRequestFullScreen();
            } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
                elem.webkitRequestFullscreen();
            } else if (elem.msRequestFullscreen) { /* IE/Edge */
                elem.msRequestFullscreen();
            }
        } else {
            if (document.exitFullscreen) {
                document.exitFullscreen();
            } else if (document.mozCancelFullScreen) { /* Firefox */
                document.mozCancelFullScreen();
            } else if (document.webkitExitFullscreen) { /* Chrome, Safari and Opera */
                document.webkitExitFullscreen();
            } else if (document.msExitFullscreen) { /* IE/Edge */
                document.msExitFullscreen();
            }
        }
    }
    nextItem() {
        if (this.state.item == (stt.length - 1)) {
            this.fullScreen(false)
            this.setState({
                page: 'home',
                item: 0
            })
            alert('Đã xong')
        } else {
            this.setState({
                item: this.state.item + 1
            })
        }
    }
    yhct_on(json) {
        this.fullScreen(true)
        this.setState({
            page: 'yhct_on',
        })
        noi12_json = require('./' + json + '.json');
        for (let i = 0; i < noi12_json.length; i++) {
            stt[i] = i
        }
        for (let i = stt.length - 1; i > 0; i--) {
            let j = Math.floor(Math.random() * (i + 1));
            let temp = stt[i];
            stt[i] = stt[j];
            stt[j] = temp;
        }
    }

    render() {
        if (this.state.page == 'home') {
            return (
                <div>
                    <button className='App-btn' onClick={() => this.yhct_on('viemphoi')}><span>Viêm phổi</span></button>
                    <button className='App-btn' onClick={() => this.yhct_on('hen')}><span>Hen</span></button>
                    <button className='App-btn' onClick={() => this.yhct_on('viempqcap')}><span>Viêm phế quản cấp</span></button>
                    <button className='App-btn' onClick={() => this.yhct_on('thaptim')}><span>Thấp tim</span></button>
                    <button className='App-btn' onClick={() => this.yhct_on('ttp')}><span>Thuyên tắt Phổi</span></button>
                </div>
            );
        } else {
            // let item = noi12_json[stt[this.state.item]];
            let item = noi12_json[this.state.item]
            let btns = [...item.a.split("|"), ...item.n.split("|")]
            for (let i = btns.length - 1; i > 0; i--) {
                let j = Math.floor(Math.random() * (i + 1));
                let temp = btns[i];
                btns[i] = btns[j];
                btns[j] = temp;
            }
            if (item.q.length == 0) {
                console.log(item)
                alert('Lỗi không có câu hỏi')
            }
            if (item.c.length == 0){
                console.log(item)
                alert('Lỗi không có loại câu')
            }
            let img;
            let count_true = [];
            // if (item.s!="") img = require('./img/' + item.s);
            if (item.c == "0"){
                return (
                    <div className="MD-on">
                        <h5>Câu {this.state.item + 1}/{stt.length}: {item.q}</h5>
                        {
                            img != undefined && <img src={img} className='Kisinh-img' />
                        }   
                        {
                            btns.map((i, index) => {
                                if (i == item.a) {
                                    return (
                                        <button className='MD-btn' key={this.state.item + '_' + index} onClick={() => this.nextItem()}>{i}</button>
                                    )
                                } else {
                                    return (
                                        <button className='MD-btn' key={this.state.item + '_' + index} onClick={(evt) => {
                                            stt.push(this.state.item);
                                            evt.target.className = 'MD-btn false'
                                        }}>{i}</button>
                                    )
                                }
                            })
                        }
                    </div>
                )   
            }
            if( item.c =="1"){
                return (
                    <div className="MD-on">
                        <h5>Câu {this.state.item + 1}/{stt.length}: {item.q}</h5>
                        <h5>Chọn nhiều đáp án</h5>
                        {
                            img != undefined && <img src={img} className='Kisinh-img' />
                        }   
                        {
                            btns.map((i, index) => {
                                let pos= item.a.split("|").findIndex(e=>{return(e==i)})
                                if (pos!=-1) {
                                    return (
                                        <>
                                     
                                        <button className='MD-btn' key={this.state.item + '__' + index} onClick={(evt) => {                                          
                                            if (!count_true.includes(i)){
                                                count_true.push(i)
                                                evt.target.className = 'MD-btn true'
                                                if(count_true.length==item.a.split("|").length){
                                                    this.nextItem()
                                                }
                                            }                                       
                                        }}>{i}</button>
                                        </>
                                    )
                                } else {
                                    return (
                                        <button className='MD-btn' key={this.state.item + '_' + index} onClick={(evt) => {   
                                            stt.push(this.state.item);
                                            evt.target.className = 'MD-btn false'
                                        }}>{i}</button>
                                    )
                                }
                            })
                        }
                    </div>
                )
            }
            
        }
    }
}

export default noi12;