import React, { Component } from 'react';
import logo from './logo.svg';
import Kisinh from './pages/kisinh/Kisinh';
import Knls from './pages/knls/Knls';
import Phoi from './pages/phoi/Phoi';
import MD from './pages/md/MD';
import YHCT from './pages/yhct/YHCT'
import Noi12 from './pages/noi12/noi12'
import './App.css';

class App extends Component {
  constructor() { 
    super()
    this.state = {
      page: 'app',
      title: 'Group 10 review',
      bg: ''
    }
  }
  phoi(){
    this.setState({
      page: 'phoi',
      title: 'Phôi',
      bg: ' gp2'
    }) 
  }
  gp2(){
    this.setState({
      page: 'gp2',
      title: 'Giải phẫu 2',
      bg: ' gp2'
    }) 
  }
  ks() {
    this.setState({
      page: 'ks',
      title: 'Kí sinh',
      bg: ' ks'
    })
  }
  md(){
    this.setState({
      page: 'md',
      title: 'Miễn dịch',
      bg: ' md'
    })
  }
  yhct(){
    this.setState({
      page: 'yhct',
      title: 'Y học cổ truyền',
      bg: ' md'
    })
  }
  knls() {
    this.setState({ page: 'knls',
    title: 'Kĩ năng lâm sàng',
    bg: ' knls' })
  }
  ddcb() {
    this.setState({ page: 'ddcb' })
  }
  noi12(){
    this.setState({ page: 'noi12' })
  }
  checkState() {
    switch (this.state.page) {
      case 'app': return (
        <div>
          <p>Năm 3</p>
          <button className='App-btn' onClick={() => this.noi12()}><span>Nội 1-2</span></button> 
          <button className='App-btn' onClick={() => this.yhct()}><span>Y học cổ truyền</span></button> 
          <hr></hr>
          <p>Năm 2</p>
          <button className='App-btn' onClick={() => this.md()}><span>Miễn dịch</span></button>
          <button className='App-btn' onClick={() => this.ks()}><span>Kí sinh</span></button>
          <button className='App-btn' onClick={() => this.knls()}><span>Kĩ năng lâm sàng</span></button>
          <button className='App-btn' onClick={() => this.phoi()}><span>Phôi</span></button>
          {/* <button className='App-btn' onClick={() => this.ddcb()}><span>Điều dưỡng cơ bản</span></button> */}
        </div>
      )
      case 'md': return(
        <MD />
      )
      case 'ks': return (
        <Kisinh />
      )
      case 'knls':return(
        <Knls/>
      )
      case 'phoi':return(
        <Phoi/>
      )
      case 'yhct':return(
        <YHCT/>
      )
      case 'noi12':return(
        <Noi12/>
      )
    }
  }
  render() {
    return (
      <div className={'App' + this.state.bg}>
        <header className='App-header'>
          <img src={logo} className="App-logo" alt="logo" />
          <p >{this.state.title}</p>
        </header>
        <div className='container'>
          {this.checkState()}
        </div>
      </div>
    );
  }
}

export default App;