import React, { Component } from 'react';
import logo from './../../logo.svg';
import './Login.css'
import firebase from 'firebase';
firebase.initializeApp({
    apiKey: "AIzaSyCR22TBV4Qz6I598UzEpnx_5pWM1wPCxh4",
    authDomain: "heffortsuccess.firebaseapp.com",
    databaseURL: "https://heffortsuccess.firebaseio.com",
    projectId: "heffortsuccess",
    storageBucket: "heffortsuccess.appspot.com",
    messagingSenderId: "453829366395",
    appId: "1:453829366395:web:83e13d4b6d0f46bb8708de",
    measurementId: "G-B3R6TDDMMD"
});
class Login extends Component {
    setCookie(cname, cvalue, exdays) {
        let d = new Date();
        d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
        let expires = "expires=" + d.toUTCString();
        document.cookie = cname + "=" + cvalue + "; " + expires;
    }
    openDialog() {
        firebase.auth().signInWithPopup(new firebase.auth.FacebookAuthProvider()).then((result) => {
            let user = result.user;
            this.setCookie('id', 1, 5);
            console.log(user);
            this.props.accept_login();
        }).catch(function (error) {
        });
    }
    render() {
        return (
            <header className='Login-header'>
                <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css"></link>
                <img src={logo} className='Login-logo' alt='logo' />
                <h1>WELCOME TO 3H EFFORT AND SUCESS</h1>
                <h2>👍 website cần xác thực danh tính của bạn 👌</h2>
                <div className='col'>
                    <a className="google btn">
                        <i className="fa fa-google fa-fw"></i> Login with Google
                        </a>
                    <a className="fb btn" onClick={() => this.openDialog()}>
                        <i className="fa fa-facebook fa-fw"></i> Login with Facebook
                        </a>
                </div>

            </header>
        )
    }
}
export default Login