import React, { Component } from 'react';
import './MD.css'
var yhct_json;
var stt = [];
class YHCT extends Component {
    constructor() {
        super()
        this.state = {
            page: 'home',
            item: 0
        }
    }
    fullScreen(b) {
        let elem = document.documentElement;
        if (b) {
            if (elem.requestFullscreen) {
                elem.requestFullscreen();
            } else if (elem.mozRequestFullScreen) { /* Firefox */
                elem.mozRequestFullScreen();
            } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
                elem.webkitRequestFullscreen();
            } else if (elem.msRequestFullscreen) { /* IE/Edge */
                elem.msRequestFullscreen();
            }
        } else {
            if (document.exitFullscreen) {
                document.exitFullscreen();
            } else if (document.mozCancelFullScreen) { /* Firefox */
                document.mozCancelFullScreen();
            } else if (document.webkitExitFullscreen) { /* Chrome, Safari and Opera */
                document.webkitExitFullscreen();
            } else if (document.msExitFullscreen) { /* IE/Edge */
                document.msExitFullscreen();
            }
        }
    }
    nextItem() {
        if (this.state.item == (stt.length - 1)) {
            this.fullScreen(false)
            this.setState({
                page: 'home',
                item: 0
            })
            alert('Đã xong')
        } else {
            this.setState({
                item: this.state.item + 1
            })
        }
    }
    yhct_on(json) {
        this.fullScreen(true)
        this.setState({
            page: 'yhct_on',
        })
        yhct_json = require('./' + json + '.json');
        for (let i = 0; i < yhct_json.length; i++) {
            stt[i] = i
        }
        for (let i = stt.length - 1; i > 0; i--) {
            let j = Math.floor(Math.random() * (i + 1));
            let temp = stt[i];
            stt[i] = stt[j];
            stt[j] = temp;
        }
    }

    render() {
        if (this.state.page == 'home') {
            return (
                <div>
                    <button className='App-btn' onClick={() => this.yhct_on('phuongduoc')}><span>Phương dược</span></button>
                    <button className='App-btn' onClick={() => this.yhct_on('hethonghuyet')}><span>Hệ thống huyệt</span></button>
                </div>
            );
        } else {
            let item = yhct_json[stt[this.state.item]];
            let btns = [item.a, ...item.n.split("|")]
            for (let i = btns.length - 1; i > 0; i--) {
                let j = Math.floor(Math.random() * (i + 1));
                let temp = btns[i];
                btns[i] = btns[j];
                btns[j] = temp;
            }
            if (item.q.length == 0) {
                this.nextItem()
            }
            let img;
            if (item.s!="") img = require('./img/' + item.s);
            return (
                <div className="MD-on">
                    <h5>Câu {this.state.item + 1}/{stt.length}: {item.q}</h5>
                    {
                        img != undefined && <img src={img} className='Kisinh-img' />
                    }   
                    {
                        btns.map((i, index) => {
                            if (i == item.a) {
                                return (
                                    <button className='MD-btn' key={this.state.item + '_' + index} onClick={() => this.nextItem()}>{i}</button>
                                )
                            } else {
                                return (
                                    <button className='MD-btn' key={this.state.item + '_' + index} onClick={(evt) => {
                                        stt.push(stt[this.state.item]);
                                        evt.target.className = 'MD-btn false'
                                    }}>{i}</button>
                                )
                            }
                        })
                    }
                </div>
            )
        }
    }
}

export default YHCT;