import React, { Component } from 'react';
import './Knls.css'
var knls_json;
class Knls extends Component {
    constructor() {
        super();
        this.state = {
            choose: 'home',
            item: 0
        }
    }
    fullScreen(b) {
        let elem = document.documentElement;
        if (b) {
            if (elem.requestFullscreen) {
                elem.requestFullscreen();
            } else if (elem.mozRequestFullScreen) { /* Firefox */
                elem.mozRequestFullScreen();
            } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
                elem.webkitRequestFullscreen();
            } else if (elem.msRequestFullscreen) { /* IE/Edge */
                elem.msRequestFullscreen();
            }
        } else {
            if (document.exitFullscreen) {
                document.exitFullscreen();
            } else if (document.mozCancelFullScreen) { /* Firefox */
                document.mozCancelFullScreen();
            } else if (document.webkitExitFullscreen) { /* Chrome, Safari and Opera */
                document.webkitExitFullscreen();
            } else if (document.msExitFullscreen) { /* IE/Edge */
                document.msExitFullscreen();
            }
        }
    }
    knvk(c) {
        if (c == null) {
            this.setState({
                choose: 'knvk'
            })
        } else {
            this.fullScreen(true)
            knls_json = require('./' + c + '.json');
            this.setState({
                choose: 'knvk-on'
            })
        }

    }
    nextItem() {
        if (this.state.item == (knls_json.length - 1)) {
            this.fullScreen(false)
            this.setState({
                choose: 'home',
                item: 0
            })
            alert('Đã xong')
        } else {
            this.setState({
                item: this.state.item + 1
            })
        }
    }
    backItem(){
        if (this.state.item == 0) {
            this.fullScreen(false)
            this.setState({
                choose: 'home',
                item: 0
            })
        } else {
            this.setState({
                item: this.state.item - 1
            })
        }
    }
    render() {
        switch (this.state.choose) {
            case 'home': {
                return (
                    <div>
                        <button className='App-btn' onClick={() => this.knvk()} ><span>Kĩ năng vô khuẩn</span></button>
                    </div>
                );
            }
            case 'knvk': {
                return (
                    <div>
                        <button className='App-btn' onClick={() => this.knvk('rtnk')} ><span>Rửa tay thường quy (nội khoa)</span></button>
                        <button className='App-btn' onClick={() => this.knvk('mapt')}><span>Mặc áo phẫu thuật</span></button>
                    </div>
                );
            }
            case 'knvk-on': {
                let i= this.state.item;
                let img = require('./img/' + knls_json[i].s);
                return (
                    <div className='Knls-on'>
                        <p>Bước {i+1}/{knls_json.length}: {knls_json[i].p}</p>
                        <img className='Knls-img' src={img}/>
                        <button className='Knls-btn' onClick={()=>this.nextItem()}><span>Bước tiếp theo</span></button>
                        <button className='Knls-btn' onClick={()=>this.backItem()}><span>Lùi lại</span></button>
                    </div>
                );
            }
            default: {
                return (
                    <p>Lỗi</p>
                )
            }
        }

    }
}

export default Knls;