import React, { Component } from 'react';
import './Phoi.css'
var phoi_json;
var stt = [];
class Phoi extends Component {
  constructor() {
    super()
    this.state = {
      page: 'home',
      item: 0
    }
  }
  fullScreen(b) {
    let elem = document.documentElement;
    if (b) {
      if (elem.requestFullscreen) {
        elem.requestFullscreen();
      } else if (elem.mozRequestFullScreen) { /* Firefox */
        elem.mozRequestFullScreen();
      } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
        elem.webkitRequestFullscreen();
      } else if (elem.msRequestFullscreen) { /* IE/Edge */
        elem.msRequestFullscreen();
      }
    } else {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      } else if (document.mozCancelFullScreen) { /* Firefox */
        document.mozCancelFullScreen();
      } else if (document.webkitExitFullscreen) { /* Chrome, Safari and Opera */
        document.webkitExitFullscreen();
      } else if (document.msExitFullscreen) { /* IE/Edge */
        document.msExitFullscreen();
      }
    }
  }
  nextItem() {
    if (this.state.item == (stt.length - 1)) {
      this.fullScreen(false)
      this.setState({
        page: 'home',
        item: 0
      })
      alert('Đã xong')
    } else {
      this.setState({
        item: this.state.item + 1
      })
    }
  }
  phoi_on(json) {
    this.fullScreen(true)
    this.setState({
      page: 'phoi_on',
    })
      phoi_json = require('./' + json + '.json');
    for (let i = 0; i < phoi_json.length; i++) {
      stt[i] = i
    }
    for (let i = stt.length - 1; i > 0; i--) {
      let j = Math.floor(Math.random() * (i + 1));
      let temp = stt[i];
      stt[i] = stt[j];
      stt[j] = temp;
    }
  }
  loadimg(s) {
    if (s.length != 0) {
      let img = require('./img/' +  s);
      console.log('ok')
      return (<img src={img} className='Phoi-img' />)
    }
  }
  render() {
    if (this.state.page == 'home') {
      return (
        <div>
          <button className='App-btn' onClick={() => this.phoi_on(2)}><span>Sự thụ tinh</span></button>
          <button className='App-btn' onClick={() => this.phoi_on(3)}><span>Sự làm tổ</span></button>
          <button className='App-btn' onClick={() => this.phoi_on(4)}><span>Phôi ba lá</span></button>
          <button className='App-btn' onClick={() => this.phoi_on(8)}><span>Hệ tim mạch</span></button>
          <button className='App-btn' onClick={() => this.phoi_on(9)}><span>Hệ tiêu hóa</span></button>
          <button className='App-btn' onClick={() => this.phoi_on(10)}><span>Hệ tiết niệu</span></button>
          <button className='App-btn' onClick={() => this.phoi_on(13)}><span>Sự hình thành các cơ quan vùng đầu và cổ</span></button>
          <button className='App-btn' onClick={() => this.phoi_on(100)}><span>Tổng hết</span></button>
        </div>
      );
    } else {
      let item = phoi_json[stt[this.state.item]];
      let btns = [item.a, ...item.n.split("|")]
      for (let i = btns.length - 1; i > 0; i--) {
        let j = Math.floor(Math.random() * (i + 1));
        let temp = btns[i];
        btns[i] = btns[j];
        btns[j] = temp;
      }
      if (item.q.length==0){
        this.nextItem()
      }
      return (
        <div className="Phoi-on">
          <p>Câu {this.state.item + 1}/{stt.length}: {item.q}</p>
          {this.loadimg(item.s)}
          {
            btns.map((i, index) => {
              if (i == item.a) {
                return (
                  <button className='Phoi-btn' key={this.state.item + '_' + index} onClick={() => this.nextItem()}>{i}</button>
                )
              } else {
                return (
                  <button className='Phoi-btn' key={this.state.item + '_' + index} onClick={(evt) => {
                    stt.push(stt[this.state.item]);
                    evt.target.className = 'Phoi-btn false'
                  }}>{i}</button>
                )
              }
            })
          }
        </div>
      )
    }
  }
}

export default Phoi;